
    <template>
      <section class="market-doc-container">
        <h2>Message 消息提示</h2>
<h3>基本使用</h3>
<p>基本的提示，支持四种不同的提示，默认1.5秒之后关闭</p>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-button @click=&quot;message('info')&quot;&gt;info 消息提示&lt;/mku-button&gt;
  &lt;mku-button @click=&quot;message('error')&quot;&gt;error 消息提示&lt;/mku-button&gt;
  &lt;mku-button @click=&quot;message('success')&quot;&gt;success 消息提示&lt;/mku-button&gt;
  &lt;mku-button @click=&quot;message('warning')&quot;&gt;warning 消息提示&lt;/mku-button&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  methods: {
    message (type) {
      this.$message[type](`这是一条${type}消息，默认1.5s之后隐藏`)
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>自定义显示时长</h3>
<p>支持自定义显示时间，默认显示1.5s，支持其他时长或者永久显示</p>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-button @click=&quot;message1&quot;&gt;默认1.5s&lt;/mku-button&gt;
  &lt;mku-button @click=&quot;message2&quot;&gt;设置显示时间为3s&lt;/mku-button&gt;
  &lt;mku-button @click=&quot;message3&quot;&gt;不自动关闭，可以显示关闭按钮&lt;/mku-button&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  methods: {
    message1 () {
      this.$message.success('这是一条默认的1.5s提示')
    },
    message2 () {
      this.$message.success({
        title: '这是一条3s后关闭的提示',
        duration: 3000
      })
    },
    message3 () {
      this.$message.success({
        title: '这是一条不会自动关闭的提示',
        duration: 0,
        closeable: true
      })
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>渲染html</h3>
<p>支持自定义显示时间，默认显示1.5s，支持其他时长或者永久显示</p>
<div class="mku-doc__warning">
<p>动态渲染HTML容易导致XSS攻击。应确保render的值是安全可靠的，禁止将用户提交的内容赋值给该属性。</p>
</div>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-button @click=&quot;message&quot;&gt;自定义html&lt;/mku-button&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      nums: 11
    }
  },
  methods: {
    message () {
      const html = `本次共提交&lt;i style=&quot;color: red;&quot;&gt; ${this.nums} &lt;/i&gt;条数据`
      this.$message.success({
        render: html
      })
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>Message Attributes</h3>
<table>
