<td style="text-align:left">以v-html方式渲染render的内容，设置该属性时自动忽略content内容</td>
<td style="text-align:left">String</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">duration</td>
<td style="text-align:left">提示的时间</td>
<td style="text-align:left">Number</td>
<td style="text-align:left">-</td>
<td style="text-align:left">1500</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">closeable</td>
<td style="text-align:left">是否显示关闭按钮</td>
<td style="text-align:left">Boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">onShow</td>
<td style="text-align:left">显示的回调函数</td>
<td style="text-align:left">Function</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">onClose</td>
<td style="text-align:left">关闭的回调函数</td>
<td style="text-align:left">Function</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
